import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { SingaporeNricComponent } from './singapore-nric.component';

const routes: Routes = [
  { path: '', component: SingaporeNricComponent },
  { path: 'generator', loadChildren: () => import('./generator/generator.module').then(m => m.GeneratorModule) },
  { path: 'validator', loadChildren: () => import('./validator/validator.module').then(m => m.ValidatorModule) }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class SingaporeNricRoutingModule { }
